window.socket = io(); // Socket global verf√ºgbar machen
const canvas = document.getElementById('gameCanvas');
const ctx = canvas.getContext('2d');
const teamSelection = document.getElementById('teamSelection');

let players = {};
let projectiles = [];
let walls = [];
let flag = {};
let score = { red: 0, blue: 0 };
let redSpawn = {};
let blueSpawn = {};
let gameRunning = false;

const keys = {};
document.addEventListener('keydown', (e) => {
    // üîπ Bewegung (WASD-Steuerung)
    if (["w", "a", "s", "d"].includes(e.key)) {
        keys[e.key] = true;
    }

    // üîπ Schie√üen (Leertaste)
    if (e.key === " " && canShoot) {
        const angle = Math.atan2(mouseY - playerY, mouseX - playerX);

        if (isNaN(angle)) {
            console.error("‚ùå Fehler: `angle` ist NaN! Mausposition k√∂nnte falsch sein.");
            return;
        }

        console.log(`üî´ Schie√üen mit Winkel: ${angle}`);
        socket.emit('shoot', { angle });

        canShoot = false;
        setTimeout(() => canShoot = true, 500);
    }
});

// üîπ Bewegung stoppen, wenn Taste losgelassen wird
document.addEventListener('keyup', (e) => {
    if (["w", "a", "s", "d"].includes(e.key)) {
        keys[e.key] = false;
    }
});

const chatInput = document.getElementById('chatInput');
const chatMessagesContainer = document.getElementById('chatMessages');

document.getElementById('chatForm').addEventListener('submit', (e) => {
    e.preventDefault(); // Verhindert das Neuladen der Seite
    const message = chatInput.value.trim();
    if (message) {
        console.log("üì§ Nachricht senden:", message);
        socket.emit('chatMessage', message);
        chatInput.value = '';  // Eingabefeld leeren
    }
});

// Empf√§ngt Chat-Nachrichten vom Server
socket.on('chatUpdate', (messages) => {
    console.log("üì• Chat-Nachrichten empfangen:", messages);
    chatMessagesContainer.innerHTML = ''; // Chat leeren
    messages.forEach(msg => {
        const li = document.createElement('li');
        li.textContent = msg;
        chatMessagesContainer.appendChild(li);
    });
    chatMessagesContainer.scrollTop = chatMessagesContainer.scrollHeight; // Auto-Scroll
});



let canShoot = true;

function chooseTeam(team) {
    const usernameInput = document.getElementById('username');
    if (!usernameInput) {
        console.error("Fehler: Username-Feld existiert nicht!");
        return;
    }

    const username = usernameInput.value.trim();
    if (!username) {
        alert("Bitte gib einen Namen ein!");
        return;
    }

    console.log(`üì¢ Sende Team-Wahl: { username: "${username}", team: "${team}" }`);
    socket.emit('chooseTeam', { username, team });

    document.getElementById('teamSelection').style.display = 'none';
    document.getElementById('gameCanvas').style.display = 'block';
}

function gameLoop() {
    if (keys['w']) socket.emit('move', { dx: 0, dy: -3 });
    if (keys['a']) socket.emit('move', { dx: -3, dy: 0 });
    if (keys['s']) socket.emit('move', { dx: 0, dy: 3 });
    if (keys['d']) socket.emit('move', { dx: 3, dy: 0 });

    if (keys[' '] && canShoot) {
        socket.emit('shoot');
        canShoot = false;
        setTimeout(() => canShoot = true, 1000);
    }

    render();
    requestAnimationFrame(gameLoop);
}

function render() {
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    // Spawnzonen
    ctx.fillStyle = 'rgba(255, 0, 0, 0.3)';
    ctx.fillRect(redSpawn.x - 50, redSpawn.y - 50, 100, 100);
    ctx.fillStyle = 'rgba(0, 0, 255, 0.3)';
    ctx.fillRect(blueSpawn.x - 50, blueSpawn.y - 50, 100, 100);

    // W√§nde
    ctx.fillStyle = 'grey';
    walls.forEach(wall => ctx.fillRect(wall.x, wall.y, wall.width, wall.height));

    // Spieler rendern
    for (const id in players) {
        const player = players[id];

        // Zeichne Spieler-Kreis
        ctx.fillStyle = player.team === 'red' ? 'red' : 'blue';
        ctx.beginPath();
        ctx.arc(player.x, player.y, 10, 0, Math.PI * 2);
        ctx.fill();

        // Falls der Spieler die Flagge hat, Flagge anzeigen
        if (player.hasFlag) {
            ctx.fillStyle = 'yellow';
            ctx.fillRect(player.x - 5, player.y - 20, 10, 10);
        }

        const heartX = player.x - 10;  // Startposition f√ºr die Anzeige
        const heartY = player.y + 15;  // Unterhalb des Spielers
        
        // ‚ù§Ô∏è Erst Leben zeichnen
        ctx.fillStyle = "red";
        for (let i = 0; i < player.health; i++) {
            ctx.fillRect(heartX + i * 12, heartY, 10, 10);
        }
        
        // üíô Dann R√ºstung direkt rechts neben das letzte Herz zeichnen
        const armorX = heartX + (player.health * 12);  // Setzt Armor nach die Herzen
        ctx.fillStyle = "blue";
        for (let i = 0; i < player.armor; i++) {
            ctx.fillRect(armorX + i * 12, heartY, 10, 10);
        }

 

        // **Name √ºber dem Spieler anzeigen**
        if (player.username) {
            ctx.fillStyle = "black";
            ctx.font = "14px Arial";
            ctx.textAlign = "center";
            ctx.fillText(player.username, player.x, player.y - 15); // Name leicht √ºber Spieler
        }
    }

    // Projektile
    ctx.fillStyle = 'black';
    projectiles.forEach(proj => {
        ctx.beginPath();
        ctx.arc(proj.x, proj.y, 5, 0, Math.PI * 2);
        ctx.fill();
    });

    // Flagge
    ctx.fillStyle = 'yellow';
    if (!flag.holder) {
        ctx.fillRect(flag.x - 10, flag.y - 10, 20, 20);
    }

    // **Medikit Rendern**
    ctx.fillStyle = 'green';
    medikits.forEach(medikit => {
    if (medikit.active) {
            ctx.fillRect(medikit.x - 5, medikit.y - 5, 10, 10);
            }
        });

    
    // **R√ºstung Rendern**
        ctx.fillStyle = 'brown';
        armors.forEach(armor => {
        if (armor.active) {
                ctx.fillRect(armor.x - 5, armor.y - 5, 10, 10);
                }
            });

    // Punktestand
    ctx.fillStyle = 'black';
    ctx.font = '20px Arial';
    ctx.fillText(`Rot: ${score.red} | Blau: ${score.blue}`, 700, 20);
}



socket.on('state', (state) => {
    console.log("üì¢ Spielstatus empfangen:", state);  // Debug-Log

    players = state.players;
    projectiles = state.projectiles;
    walls = state.walls;
    flag = state.flag;
    score = state.score;
    redSpawn = state.redSpawn;
    blueSpawn = state.blueSpawn;

    // Falls das Game-Loop nicht l√§uft, starte es!
    if (!gameRunning) {
        gameRunning = true;
        gameLoop();
    }
});

socket.on('gameOver', (winner) => {
    alert(`${winner === 'red' ? 'Rotes Team' : 'Blaues Team'} hat gewonnen!`);
    location.reload();
});

let mouseX = 0;
let mouseY = 0;
let playerX = 0;
let playerY = 0;

canvas.addEventListener('mousemove', (event) => {
    const rect = canvas.getBoundingClientRect();
    mouseX = event.clientX - rect.left;
    mouseY = event.clientY - rect.top;
});

socket.on('state', (state) => {
    if (socket.id in state.players) {
        playerX = state.players[socket.id].x;
        playerY = state.players[socket.id].y;
    }
});

document.addEventListener('click', () => {
    if (canShoot) {
        const angle = Math.atan2(mouseY - playerY, mouseX - playerX);
        socket.emit('shoot', { angle });
        canShoot = false;
        setTimeout(() => canShoot = true, 500);
    }
});



let medikits = [];

socket.on('updateMedikits', (serverMedikits) => {
    medikits = serverMedikits;
});

let armors = [];

socket.on('updateArmors', (serverArmors) => {
    armors = serverArmors;
});

